@charset "UTF-8";
@font-face {
  font-family: 'iconfont';
  src: url("../fonts/iconfont.eot");
  /* IE9*/
  src: url("../fonts/iconfont.eot#iefix") format("embedded-opentype"), url("../fonts/iconfont.woff") format("woff"), url("../fonts/iconfont.ttf") format("truetype"), url("../fonts/iconfont.svg#iconfont") format("svg");
  /* iOS 4.1- */ }

body,
html {
  position: relative; }

.radio-wrap,
.check-wrap {
  display: inline-block;
  line-height: 20px; }

.radio-box,
.check-box {
  display: inline-block;
  margin-right: 15px; }

.radio-box input[type='radio'] {
  display: none; }
  .radio-box input[type='radio'] + i {
    display: inline-block;
    width: 20px;
    height: 20px;
    background: url(../img/radio.png);
    margin-right: 5px;
    vertical-align: top; }
  .radio-box input[type='radio']:checked + i {
    background: url(../img/radio_active.png); }

.check-box input[type='checkbox'] {
  display: none; }
  .check-box input[type='checkbox'] + i {
    display: inline-block;
    width: 20px;
    height: 20px;
    background: url(../img/check.png);
    margin-right: 5px;
    vertical-align: top; }
  .check-box input[type='checkbox']:checked + i {
    background: url(../img/check_active.png); }

/*顶部*/
.b-header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 99;
  padding: 13px 20px;
  height: 79px;
  min-width: 1190;
  background: #fff;
  *zoom: 1; }
  .b-header:after, .b-header:before {
    content: '';
    display: table;
    font: 0/0 a; }
  .b-header:after {
    clear: both; }
  .b-header:after {
    content: '';
    display: inline-block;
    position: absolute;
    z-index: -1;
    left: 0;
    right: 0;
    bottom: 0;
    height: 1px;
    background: #efefef; }

.h-left {
  float: left; }

.h-right {
  float: right; }

.h-main {
