.nav-icon {
  width: 54px;
  height: 54px;
  display: inline-block;
  background-repeat: no-repeat;
  background-size: inherit;
  text-align: center; }
  .nav-icon:after {
    font-family: iconfont;
    font-size: 36px;
    line-height: 54px;
    font-style: normal;
    color: #999; }

.nav-service:after {
  content: '\e61f'; }

.nav-news:after {
  content: '\e620'; }

.nav-help:after {
  content: '\e622'; }

.nav-setup:after {
  content: '\e60b'; }

.nav-logout:after {
  content: '\e62c'; }

.nav-home:after {
  content: '\e600'; }

.dropdown-box {
  position: absolute;
  display: none;
  z-index: 10;
  top: 55px;
  padding: 2px;
  width: 135px;
  background: #fff;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15); }
  .nav-li .dropdown-box {
    right: -35px;
    left: auto; }
  .dropdown-box.active {
    display: block; }

a.dropli {
